import React from "react";
import "./style.css";

function NarutoCharacters(props) {
  return (
    <div className="card">
      <div className="img-container">
        <img alt={props.name} src={props.image} onClick={
          () => {
            props.clickHandler(props.id)
          }
        }/>
      </div>
    </div>
  );
}

export default NarutoCharacters;
